@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
  height: 100vh;
  color: white;
  text-align: center;
  background-color: #cecece; }
  .App .App_content {
    flex: 1 1;
    overflow-y: auto; }

.Header {
  flex: 0 0 auto;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.2em 10vw;
  background-color: #169cb4;
  color: #ffdd55;
  width: 100%;
  box-shadow: 0px -20px 16px 20px rgba(0, 0, 0, 0.25); }
  .Header h1 {
    margin: 0.4em 0.8em;
    font-size: 1em;
    text-align: left; }
  .Header .Header_logo {
    margin: 0.3em 0.1em;
    height: 3em; }
  .Header .Header_title {
    margin-left: 0.8em;
    height: 1.3em; }

.HeaderNavigation a {
  margin-left: 0.5em;
  padding: 0.8em 1.3em;
  border: 0.1em solid transparent;
  background-color: transparent;
  color: white;
  font-family: 'Lato', sans-serif;
  font-size: 1.1em;
  font-weight: normal;
  text-decoration: none;
  -webkit-transition: background-color 0.1s ease-out;
  transition: background-color 0.1s ease-out; }
  .HeaderNavigation a:hover {
    background-color: rgba(255, 255, 255, 0.05); }
  .HeaderNavigation a.active {
    border-color: white;
    color: white;
    background-color: rgba(255, 255, 255, 0.1);
    font-weight: bold; }
  .HeaderNavigation a.primary {
    color: #eb5626;
    background-color: #ffdd55;
    border: 1px solid #ffdd55; }
    .HeaderNavigation a.primary:hover {
      background-color: #ffe57b; }

.PacksPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 0; }
  .PacksPage .PackListEntry {
    margin: 10px 0; }

.PackListEntry {
  display: block;
  width: 500px;
  height: 150px;
  background-color: #ececec;
  color: #333333;
  box-shadow: 5px 5px 6px 0px rgba(0, 0, 0, 0.05); }
  .PackListEntry header h1 {
    margin: 0;
    padding: 0.2em 1em;
    font-size: 1.6em;
    text-align: left; }

